"use client";

import React, { useState, useEffect } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useRouter } from "next/navigation";
import { DollarLineIcon, TimeIcon, CheckCircleIcon, AlertIcon } from "@/icons";
import { formatCurrency } from "@/utils/format";
import { vendaService, vendaFinalService, type Venda, type VendaFinal } from "@/services/api/venda.service";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import toast from "react-hot-toast";

interface Stats {
  totalVendas: number;
  vendasPendentes: number;
  vendasPagas: number;
  totalFaturas: number;
  faturasPendentes: number;
  faturasPagas: number;
  valorTotalPendente: number;
  valorTotalPago: number;
}

export default function ClienteDashboardPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState<Stats>({
    totalVendas: 0,
    vendasPendentes: 0,
    vendasPagas: 0,
    totalFaturas: 0,
    faturasPendentes: 0,
    faturasPagas: 0,
    valorTotalPendente: 0,
    valorTotalPago: 0
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadDashboardData = async () => {
      try {
        setLoading(true);
        
        // Carregar vendas e vendas finais usando os serviços
        const [vendasResponse, vendasFinaisResponse] = await Promise.all([
          vendaService.list({ limit: 100 }),
          vendaFinalService.list({ limit: 100 })
        ]);
        
        const vendas: Venda[] = vendasResponse.data || [];
        const vendasFinais: VendaFinal[] = vendasFinaisResponse.data || [];

        // Calcular estatísticas
        const vendasPendentes = vendas.filter(v => v.status === 'PENDENTE').length;
        const vendasPagas = vendas.filter(v => v.status === 'PAGO').length;
        
        const faturasPendentes = vendasFinais.filter(vf => vf.status === 'PENDING').length;
        const faturasPagas = vendasFinais.filter(vf => vf.status === 'APPROVED').length;
        
        const valorTotalPendente = vendasFinais
          .filter(vf => vf.status === 'PENDING')
          .reduce((sum, vf) => sum + parseFloat(vf.valor.toString()), 0);
        
        const valorTotalPago = vendasFinais
          .filter(vf => vf.status === 'APPROVED')
          .reduce((sum, vf) => sum + parseFloat(vf.valor.toString()), 0);

        setStats({
          totalVendas: vendas.length,
          vendasPendentes,
          vendasPagas,
          totalFaturas: vendasFinais.length,
          faturasPendentes,
          faturasPagas,
          valorTotalPendente,
          valorTotalPago
        });
      } catch (error) {
        console.error('Erro ao carregar dados do dashboard:', error);
        toast.error('Erro ao carregar dados do dashboard');
      } finally {
        setLoading(false);
      }
    };

    if (user) {
      loadDashboardData();
    }
  }, [user]);

  const getStatusIcon = (status: string) => {
    switch (status) {
        case 'PAGO':
        case 'APPROVED':
          return <CheckCircleIcon className="w-5 h-5 text-green-500" />;
        case 'PENDENTE':
        case 'PENDING':
          return <TimeIcon className="w-5 h-5 text-yellow-500" />;
        case 'CANCELLED':
          return <AlertIcon className="w-5 h-5 text-red-500" />;
        default:
          return <TimeIcon className="w-5 h-5 text-gray-500" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'PAGO':
      case 'APPROVED':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
      case 'PENDENTE':
      case 'PENDING':
        return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300';
      case 'CANCELLED':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
              Olá, {user?.nome}!
            </h1>
            <p className="text-gray-600 dark:text-gray-400 mt-1">
              Bem-vindo ao seu painel de cobranças
            </p>
          </div>
          <div className="flex items-center space-x-4">
            <ThemeToggleButton />
            <button
              onClick={() => router.push('/dashboard/cliente/cobrancas')}
              className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg transition-colors"
            >
              Ver Cobranças
            </button>
            <button
              onClick={() => router.push('/dashboard/cliente/faturas')}
              className="bg-green-600 hover:bg-green-700 text-white px-4 py-2 rounded-lg transition-colors"
            >
              Ver Faturas
            </button>
          </div>
        </div>
      </div>

      {/* KPIs */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        {/* Total de Vendas */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 dark:bg-blue-900 rounded-lg">
              <DollarLineIcon className="w-6 h-6 text-blue-600 dark:text-blue-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Total de Cobranças
              </p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {stats.totalVendas}
              </p>
            </div>
          </div>
        </div>

        {/* Vendas Pendentes */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center">
            <div className="p-2 bg-yellow-100 dark:bg-yellow-900 rounded-lg">
              <TimeIcon className="w-6 h-6 text-yellow-600 dark:text-yellow-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
              Cobranças Pendentes
              </p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {stats.vendasPendentes}
              </p>
            </div>
          </div>
        </div>

        {/* Total de Faturas */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 dark:bg-green-900 rounded-lg">
              <CheckCircleIcon className="w-6 h-6 text-green-600 dark:text-green-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Total de Faturas
              </p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {stats.totalFaturas}
              </p>
            </div>
          </div>
        </div>

        {/* Faturas Pendentes */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center">
            <div className="p-2 bg-orange-100 dark:bg-orange-900 rounded-lg">
              <AlertIcon className="w-6 h-6 text-orange-600 dark:text-orange-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Faturas Pendentes
              </p>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">
                {stats.faturasPendentes}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Valores */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Valor Pendente */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Valor Total Pendente
              </p>
              <p className="text-3xl font-bold text-yellow-600 dark:text-yellow-400">
                {formatCurrency(stats.valorTotalPendente)}
              </p>
            </div>
            <TimeIcon className="w-8 h-8 text-yellow-500" />
          </div>
        </div>

        {/* Valor Pago */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-600 dark:text-gray-400">
                Valor Total Pago
              </p>
              <p className="text-3xl font-bold text-green-600 dark:text-green-400">
                {formatCurrency(stats.valorTotalPago)}
              </p>
            </div>
            <CheckCircleIcon className="w-8 h-8 text-green-500" />
          </div>
        </div>
      </div>

      {/* Ações Rápidas */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
          Ações Rápidas
        </h3>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <button
            onClick={() => router.push('/dashboard/cliente/cobrancas')}
            className="flex items-center p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg hover:bg-blue-100 dark:hover:bg-blue-900/30 transition-colors"
          >
            <DollarLineIcon className="w-6 h-6 text-blue-600 dark:text-blue-400 mr-3" />
            <div className="text-left">
              <p className="font-medium text-gray-900 dark:text-white">
                Ver Cobranças
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Visualize suas vendas
              </p>
            </div>
          </button>

          <button
            onClick={() => router.push('/dashboard/cliente/faturas')}
            className="flex items-center p-4 bg-green-50 dark:bg-green-900/20 rounded-lg hover:bg-green-100 dark:hover:bg-green-900/30 transition-colors"
          >
            <CheckCircleIcon className="w-6 h-6 text-green-600 dark:text-green-400 mr-3" />
            <div className="text-left">
              <p className="font-medium text-gray-900 dark:text-white">
                Ver Faturas
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Visualize suas faturas
              </p>
            </div>
          </button>

          <button
            onClick={() => router.push('/dashboard/cliente/minha-conta')}
            className="flex items-center p-4 bg-gray-50 dark:bg-gray-700 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-600 transition-colors"
          >
            <div className="w-6 h-6 bg-gray-600 dark:bg-gray-400 rounded-full mr-3 flex items-center justify-center">
              <span className="text-white text-xs font-bold">
                {user?.nome?.charAt(0)?.toUpperCase()}
              </span>
            </div>
            <div className="text-left">
              <p className="font-medium text-gray-900 dark:text-white">
                Minha Conta
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Gerenciar perfil
              </p>
            </div>
          </button>
        </div>
      </div>
    </div>
  );
}
